@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
  min-height: 100vh;
  min-width: 100vw;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

#interactive-bg {
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
}

body, #app {
  z-index: 1;
  position: relative;
}

div#main {
  width: 100vw;
  min-height: 100vh;
  box-sizing: border-box;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
  opacity: 0;
  box-sizing: border-box;
  animation: fadeIn 1.2s .2s forwards;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  width: 100vmax;
  z-index: -2;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
  transition: color .2s;
  position: relative;
}

a:after {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 100%;
  background: #4e567e;
  transition: transform .3s;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scaleX(0);
}

a:hover:after {
  transform-origin: 0;
  transform: scaleX(1);
}

a:hover {
  color: #4e567e;
}

container {
  grid-gap: 20px;
  box-sizing: border-box;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
  display: grid;
}

.container .box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1), box-shadow .3s;
  display: flex;
}

.container .box:hover {
  transform: scale(1.04)translateY(-6px);
  box-shadow: 0 8px 32px #4e567e1f;
}

img.socialIcon {
  height: 30px;
  width: 30px;
  transition: transform .2s, filter .2s;
}

img.socialIcon:hover {
  filter: brightness(1.2) drop-shadow(0 2px 8px #4e567e44);
  transform: scale(1.18)rotate(-8deg);
}

@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(40px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 700px) {
  h1 {
    font-size: 2.5rem;
    line-height: 1.1;
  }

  h2 {
    padding-top: .5rem;
    font-size: 1.5rem;
  }

  .container {
    grid-gap: 10px;
    grid-template-columns: 1fr;
    margin: 10px;
  }

  .container .box {
    padding: 16px 8px;
  }

  section.padding {
    padding: 2rem .5rem;
  }

  .education {
    width: 100% !important;
    padding: 1.5rem .5rem !important;
  }

  .background {
    object-fit: cover;
    width: 100vw !important;
    height: 100vh !important;
  }

  #interactive-bg {
    width: 100vw !important;
    height: 100vh !important;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.1rem;
  }

  .container {
    grid-gap: 4px;
    margin: 4px;
  }

  .container .box {
    padding: 8px 2px;
  }

  section.padding {
    padding: 1rem .2rem;
  }
}

